.masonry-component {
    width: 100%;
    display: flex;
    justify-content: center;

    &_container {
        width: calc(100% - 400px);

        .masonry-component_title {
            text-align: center;
            /* H2/H2 Large */
            font-family: Poppins, sans-serif;
            font-size: 52px;
            font-style: normal;
            font-weight: 400;
            line-height: 124%;
            /* 64.48px */
            letter-spacing: -1.04px;
            background: linear-gradient(180deg, #2D3442 0%, #6C7689 100%);
            background-clip: text;
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
            margin-bottom: 24px;
        }

        .masonry-component_subtitle {
            color: #4F4B58;
            text-align: center;
            font-family: Figtree;
            font-size: 24px;
            font-style: normal;
            font-weight: 400;
            line-height: 29px;
            /* 120.833% */
            letter-spacing: -0.24px;
        }
    }

    &_cards {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 17.5px;
        margin-top: 40px;
    }

    .masonry-card {
        background: #fff;
        border-radius: 21px;
        overflow: hidden;
        padding: 39px;

        // Tarjetas que ocupan 2 columnas
        &.card-1,
        &.card-7 {
            grid-column: span 2;
        }

        .masonry_header {
            width: 100%;
            display: flex;
            justify-content: space-between;
            align-items: center;
            margin-bottom: 24px;

            .masonry_card_icon {
                display: flex;
                align-items: center;
                gap: 8px;

                img {
                    width: 32px;
                    height: 32px;
                    object-fit: contain;
                }

                .masonry-card_title {
                    color: #4F4B58;
                    font-family: Poppins, sans-serif;
                    font-size: 26.307px;
                    font-style: normal;
                    font-weight: 400;
                    line-height: 151%;
                    letter-spacing: -0.526px;
                }
            }

            .more_images {
                display: flex;
                gap: 16px;
                align-self: end;

                .additional-image {
                    width: 48px;
                    height: 48px;
                    border-radius: 8px;
                    overflow: hidden;

                    img {
                        width: 100%;
                        height: 100%;
                        object-fit: cover;
                    }
                }
            }
        }

        .masonry-card_text {
            color: #4F4B58;
            font-family: Figtree;
            font-size: 16px;
            font-style: normal;
            font-weight: 400;
            line-height: 24px;
            margin-bottom: 24px;
        }

        .masonry-card_image {
            width: 100%;
            border-radius: 12px;
            overflow: hidden;

            img {
                width: 100%;
                height: auto;
                object-fit: cover;
            }
        }
    }


    .green_icon {
        display: flex;
        width: 66.644px;
        height: 66.644px;
        justify-content: center;
        align-items: center;
        border-radius: 66.644px;
        background: #24A87C;
        box-shadow: 0px 3.57px 7.14px 0px rgba(0, 0, 0, 0.10), 0px 4.76px 9.521px 0px rgba(0, 0, 0, 0.08), 0px 1.19px 14.281px 0px rgba(0, 0, 0, 0.04);

        img {
            width: 28px;
        }
    }
}